.root {
  position: relative;
  
}

.input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 12px 20px;
  padding-left: 42px;
  width: 300px;
  border-radius: 30px;
  font-size: 16px;
  background-color: rgb(233, 230, 230);

  &:focus {
    border: 4px solid rgba(0, 0, 0, 0.2);
    // width: 400px;
  }
}

.icon {
  width: 22px;
  height: 22px;
  opacity: 0.3;
  position: absolute;
  left: 14px;
  top: 12px;

}

.clearIcon { 
width: 18px;
height: 18px;
opacity: 0.3;
position: absolute;
right: 15px;
top: 15px;
cursor: pointer;

&:hover {
  opacity: 0.8;
}
}